<template>
  <demo title="Checkbox" class="checkbox-demo">

    <demo-item name="Size" description="" :code="code" slot="left">
      <CheckBox v-model="test" size="sm"></CheckBox>
      <CheckBox v-model="test" size="md"></CheckBox>
      <CheckBox v-model="test" size="lg"></CheckBox>
      <p class="result">{{ JSON.stringify(test) }}</p>
    </demo-item>

    <demo-item name="Value" description="" :code="value_code" slot="right">
      <CheckBox v-model="test_value" value="small"></CheckBox>
      <CheckBox v-model="test_value" value="normal"></CheckBox>
      <CheckBox v-model="test_value" value="large"></CheckBox>
      <p class="result">{{ JSON.stringify(test_value) }}</p>
    </demo-item>

    <demo-item name="Status" description="" :code="disable_code" slot="left">
      <CheckBox v-model="test_disable"></CheckBox>
      <CheckBox v-model="test_disable" :disabled="true"></CheckBox>
      <p class="result">{{ JSON.stringify(test_disable) }}</p>
    </demo-item>

    <demo-item name="Color" description="" :code="color_code" slot="right">
      <CheckBox v-model="test_color"></CheckBox>
      <CheckBox v-model="test_color" color="rgb(0, 160, 233)"></CheckBox>
      <p class="result">{{ JSON.stringify(test_color) }}</p>
    </demo-item>

  </demo>
</template>

<style lang="stylus" scoped>
  @import '../../style/';
  .checkbox-demo .hiui-checkbox { margin-right:5px }
  .checkbox-demo .result { margin-top:10px;margin-bottom:0 }
</style>

<script>
  export default {
    data () {
      return {
        test: [],
        test_value: [],
        test_disable: [],
        test_color: [],
        code: `
<CheckBox v-model="test" size="sm"></CheckBox>
<CheckBox v-model="test" size="md"></CheckBox>
<CheckBox v-model="test" size="lg"></CheckBox>
        `,
        value_code: `
<CheckBox v-model="test_value" value="small" size="sm"></CheckBox>
<CheckBox v-model="test_value" value="normal" size="md"></CheckBox>
<CheckBox v-model="test_value" value="large" size="lg"></CheckBox>
        `,
        disable_code: `
<CheckBox v-model="test_disable"></CheckBox>
<CheckBox v-model="test_disable" :disabled="true"></CheckBox>
        `,
        color_code: `
<CheckBox v-model="test_color"></CheckBox>
<CheckBox v-model="test_color" color="rgb(0, 160, 233)"></CheckBox>
        `
      }
    }
  }
</script>



